import React from "react";
import "../stylesheets/navbar.scss";

class Navbar extends React.Component {
    constructor(){
        super();
        this.state = {
            isOpen: false
        }
    }

    render() {
        return (
            <nav className="navbar is-link nav-shadow" role="navigation" aria-label="main navigation">
                <div className="navbar-brand">
                    <a className="navbar-item nav-home" href="/">
                        <svg className="bi bi-house-door" width="2em" height="2em" viewBox="0 0 16 16" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                            <path fill-rule="evenodd" d="M7.646 1.146a.5.5 0 01.708 0l6 6a.5.5 0 01.146.354v7a.5.5 0 01-.5.5H9.5a.5.5 0 01-.5-.5v-4H7v4a.5.5 0 01-.5.5H2a.5.5 0 01-.5-.5v-7a.5.5 0 01.146-.354l6-6zM2.5 7.707V14H6v-4a.5.5 0 01.5-.5h3a.5.5 0 01.5.5v4h3.5V7.707L8 2.207l-5.5 5.5z" clip-rule="evenodd"/>
                            <path fill-rule="evenodd" d="M13 2.5V6l-2-2V2.5a.5.5 0 01.5-.5h1a.5.5 0 01.5.5z" clip-rule="evenodd"/>
                        </svg>
                    </a>

                    <a role="button" className={`navbar-burger ${this.state.isOpen? ' is-active' : ''}`} aria-label="menu" aria-expanded="false" data-target="navMenu" 
                    onClick={() => this.setState({ isOpen: !this.state.isOpen })} >
                        <span aria-hidden="true"></span>
                        <span aria-hidden="true"></span>
                        <span aria-hidden="true"></span>
                    </a>
                </div>

                <div id="navMenu" className={`navbar-menu ${this.state.isOpen? ' is-active nav-dropdown' : ''}`}>
                    <div className="navbar-start">
                        <a className="navbar-item" href="/coursework">
                            Coursework
                        </a>

                        <a className="navbar-item" href="/projects">
                            Projects
                        </a>

                        <a className="navbar-item" href="/maps">
                            Visitor Map
                        </a>
                    
                        <div className="navbar-item has-dropdown is-hoverable">
                            <a className="navbar-link">
                            More
                            </a>

                            <div className="navbar-dropdown">
                                <a className="navbar-item" href="/about">
                                    About Me
                                </a>
                                <hr className="navbar-divider"/>
                                <a className="navbar-item" href="/contact">
                                    Contact Me
                                </a>
                            </div>
                        </div>
                    </div>
                    <div class="navbar-end nav-icons">
                        <a className="navbar-item" rel="noopener noreferrer" href="https://github.com/contejus" target="_blank">
                            <svg role="img" width="2em" height="2em" viewBox="0 0 24 24" fill={this.state.isOpen? 'black' : 'white'} xmlns="http://www.w3.org/2000/svg">
                                <title>GitHub</title>
                                <path d="M12 .297c-6.63 0-12 5.373-12 12 0 5.303 3.438 9.8 8.205 11.385.6.113.82-.258.82-.577 0-.285-.01-1.04-.015-2.04-3.338.724-4.042-1.61-4.042-1.61C4.422 18.07 3.633 17.7 3.633 17.7c-1.087-.744.084-.729.084-.729 1.205.084 1.838 1.236 1.838 1.236 1.07 1.835 2.809 1.305 3.495.998.108-.776.417-1.305.76-1.605-2.665-.3-5.466-1.332-5.466-5.93 0-1.31.465-2.38 1.235-3.22-.135-.303-.54-1.523.105-3.176 0 0 1.005-.322 3.3 1.23.96-.267 1.98-.399 3-.405 1.02.006 2.04.138 3 .405 2.28-1.552 3.285-1.23 3.285-1.23.645 1.653.24 2.873.12 3.176.765.84 1.23 1.91 1.23 3.22 0 4.61-2.805 5.625-5.475 5.92.42.36.81 1.096.81 2.22 0 1.606-.015 2.896-.015 3.286 0 .315.21.69.825.57C20.565 22.092 24 17.592 24 12.297c0-6.627-5.373-12-12-12"/>
                            </svg>
                        </a>
                        <a className="navbar-item" rel="noopener noreferrer" href="https://github.com/contejus" target="_blank">
                            <svg width="2em" height="2em" fill={this.state.isOpen? 'black' : 'white'} role="img" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                                <title>LinkedIn</title>
                                <path d="M20.447 20.452h-3.554v-5.569c0-1.328-.027-3.037-1.852-3.037-1.853 0-2.136 1.445-2.136 2.939v5.667H9.351V9h3.414v1.561h.046c.477-.9 1.637-1.85 3.37-1.85 3.601 0 4.267 2.37 4.267 5.455v6.286zM5.337 7.433c-1.144 0-2.063-.926-2.063-2.065 0-1.138.92-2.063 2.063-2.063 1.14 0 2.064.925 2.064 2.063 0 1.139-.925 2.065-2.064 2.065zm1.782 13.019H3.555V9h3.564v11.452zM22.225 0H1.771C.792 0 0 .774 0 1.729v20.542C0 23.227.792 24 1.771 24h20.451C23.2 24 24 23.227 24 22.271V1.729C24 .774 23.2 0 22.222 0h.003z"/>
                            </svg>
                        </a>
                        <a className="navbar-item" rel="noopener noreferrer" href="https://github.com/contejus" target="_blank">
                            <svg className="bi bi-chat-fill" width="2em" height="2em" viewBox="0 0 16 16" fill={this.state.isOpen? 'black' : 'white'} xmlns="http://www.w3.org/2000/svg">
                                <title>Email</title>
                                <path d="M8 15c4.418 0 8-3.134 8-7s-3.582-7-8-7-8 3.134-8 7c0 1.76.743 3.37 1.97 4.6-.097 1.016-.417 2.13-.771 2.966-.079.186.074.394.273.362 2.256-.37 3.597-.938 4.18-1.234A9.06 9.06 0 008 15z"/>
                            </svg>
                        </a>
                        <a className="navbar-item" rel="noopener noreferrer" href="https://github.com/contejus" target="_blank">
                            <svg className="bi bi-file-earmark" width="2em" height="2em" viewBox="0 0 16 16" fill={this.state.isOpen? 'black' : 'white'} xmlns="http://www.w3.org/2000/svg">
                                <title>Resume</title>
                                <path d="M4 1h5v1H4a1 1 0 00-1 1v10a1 1 0 001 1h8a1 1 0 001-1V6h1v7a2 2 0 01-2 2H4a2 2 0 01-2-2V3a2 2 0 012-2z"/>
                                <path d="M9 4.5V1l5 5h-3.5A1.5 1.5 0 019 4.5z"/>
                            </svg>
                        </a>
                    </div>
                </div>
            </nav>
        )
    }
}

export default Navbar